<template>
  <div class="lost-item-hall">
    <h1>üîç Â§±Áâ©Â§ßÂéÖ</h1>

    <!-- Ê∑ªÂä†ÊêúÁ¥¢ÂíåÁ≠õÈÄâÂ∑•ÂÖ∑Ê†è -->
    <el-row :gutter="20" class="toolbar">
      <el-col :xs="24" :sm="12" :md="8" class="search-item">
        <el-input v-model="searchQuery" placeholder="Áâ©ÂìÅÂêçÁß∞/ÊèèËø∞..." clearable class="search-input" @clear="handleFilter"
          @keyup.enter="handleFilter">
          <template #prefix>
            <i class="el-icon-search input-icon"></i>
          </template>
        </el-input>
      </el-col>

      <el-col :xs="24" :sm="12" :md="8" class="search-item">
        <el-select v-model="filterType" placeholder="ÈÄâÊã©ÂàÜÁ±ª" clearable filterable class="type-selector"
          @change="handleFilter">
          <el-option v-for="item in categories" :key="item.value" :label="item.label" :value="item.value"
            :disabled="item.hasChildren">
            <span class="category-option" :style="{ paddingLeft: item.level * 20 + 'px' }">
              {{ item.label }}
              <i v-if="item.hasChildren" class="el-icon-folder-opened"></i>
            </span>
          </el-option>
        </el-select>
      </el-col>

      <el-col :xs="24" :sm="24" :md="8" class="search-item">
        <el-input v-model="locationQuery" placeholder="ËæìÂÖ•Âú∞ÂùÄÂÖ≥ÈîÆËØç..." clearable class="location-input"
          @clear="handleFilter" @keyup.enter="handleFilter">
          <template #prefix>
            <i class="el-icon-location-information input-icon"></i>
          </template>
          <template #append>
            <el-button type="primary" icon="el-icon-search" @click="handleFilter" />
          </template>
        </el-input>
      </el-col>
    </el-row>

    <el-table :data="filteredItems" v-loading="loading" :header-cell-style="{ background: '#f8f9fa' }">
      <template #empty>
        <div class="empty-state">
          <i class="el-icon-document-remove"></i>
          <span>ÂΩìÂâçÊ≤°ÊúâÂèØÊòæÁ§∫ÁöÑÂ§±Áâ©‰ø°ÊÅØ</span>
        </div>
      </template>
      <el-table-column prop="title" label="Áâ©ÂìÅÂêçÁß∞" min-width="180">
        <template #default="{ row }">
          <span class="text-ellipsis">{{ row.title }}</span>
        </template>
      </el-table-column>
      <el-table-column label="Á±ªÂûã" width="100">
        <template #default="{ row }">
          {{ getItemTypeLabel(row.item_type) }}
        </template>
      </el-table-column>
      <el-table-column prop="category" label="ÂàÜÁ±ª" width="120">
        <template #default="{ row }">
          <el-tag effect="plain">{{ row.category_name }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="‰∏¢Â§±Êó∂Èó¥" width="150">
        <template #default="{ row }">
          {{ formatTime(row.lost_time) }}
        </template>
      </el-table-column>
      <el-table-column label="‰∏¢Â§±Âú∞ÁÇπ" width="200">
        <template #default="{ row }">
          {{ row.location }}
        </template>
      </el-table-column>
      <el-table-column label="Áä∂ÊÄÅ" width="100" align="center">
        <template #default="{ row }">
          <el-tag :type="statusTypeMap[row.status]" effect="light" class="status-tag">
            {{ row.status }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="Êìç‰Ωú" width="100">
        <template #default="{ row }">
          <el-button type="primary" size="small" @click="viewDetails(row)">Êü•ÁúãËØ¶ÊÉÖ</el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- Â§±Áâ©ËØ¶ÊÉÖÂºπÁ™ó -->
    <el-dialog title="üîç Áâ©ÂìÅËØ¶ÊÉÖ" :visible.sync="detailDialogVisible" width="800px" custom-class="item-detail-dialog">
      <el-row :gutter="20">
        <!-- ÂõæÁâáËΩÆÊí≠Âå∫ -->
        <el-col :span="8">
          <el-carousel :interval="5000" height="300px" arrow="always">
            <el-carousel-item v-for="(img, index) in currentItem.images" :key="index">
              <el-image :src="img" fit="cover" class="detail-image">
                <div slot="error" class="image-error">
                  <i class="el-icon-picture-outline"></i>
                </div>
              </el-image>
            </el-carousel-item>
          </el-carousel>
        </el-col>
        <!-- ËØ¶ÁªÜ‰ø°ÊÅØÂå∫ -->
        <el-col :span="16">
          <el-descriptions :column="2" border label-class-name="detail-label">
            <el-descriptions-item label="Áâ©ÂìÅÂêçÁß∞">{{ currentItem.title }}</el-descriptions-item>
            <el-descriptions-item label="Áâ©ÂìÅÂàÜÁ±ª">
              {{ currentItem.category_name }}
            </el-descriptions-item>
            <el-descriptions-item label="Êèê‰∫§‰∫∫">
              <el-tooltip v-if="currentItem.user?.role === 'admin'" content="ÁÆ°ÁêÜÂëòË¥¶Âè∑">
                <i class="el-icon-s-custom"></i>
              </el-tooltip>
              {{ currentItem.user?.real_name || 'ÂåøÂêçÁî®Êà∑' }}
              <span v-if="currentItem.user" class="user-role-tag">
                ({{ roleMap[currentItem.user.role] }})
              </span>
            </el-descriptions-item>
            <el-descriptions-item label="‰∏¢Â§±Êó∂Èó¥">
              {{ formatTime(currentItem.lost_time) }}
            </el-descriptions-item>
            <el-descriptions-item label="‰∏¢Â§±Âú∞ÁÇπ">
              {{ currentItem.location }}
              <div v-if="currentItem.location_lat && currentItem.location_lng" class="detail-map-container"
                :id="'detail-map-' + currentItem.id"></div>
            </el-descriptions-item>
            <el-descriptions-item label="ÂèëÂ∏ÉÁ±ªÂûã">
              {{ currentItem.item_type === 'lost' ? 'Â§±Áâ©ÁôªËÆ∞' : 'ÊãõÈ¢ÜÁôªËÆ∞' }}
            </el-descriptions-item>
            <el-descriptions-item label="ÂΩìÂâçÁä∂ÊÄÅ">
              <el-tag :type="statusTypeMap[currentItem.status]" size="medium">
                {{ statusTextMap[currentItem.status] }}
              </el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="ÁôªËÆ∞Êó∂Èó¥">
              {{ formatTime(currentItem.created_at) }}
            </el-descriptions-item>
            <el-descriptions-item label="ËØ¶ÁªÜÊèèËø∞" :span="2">
              <pre class="description-pre">{{ currentItem.description }}</pre>
            </el-descriptions-item>
          </el-descriptions>
        </el-col>
      </el-row>
      <span slot="footer">
        <el-button type="warning" @click="sendNotification( currentItem.id )"
          >ÈÄöÁü•Â§±‰∏ª</el-button>
        <el-button :type="bookmarked ? 'success' : 'primary'"
          :icon="bookmarked ? 'el-icon-star-off' : 'el-icon-star-on'" @click="handleBookmark">
          {{ bookmarked ? 'Â∑≤Êî∂Ëóè' : 'Êî∂ËóèÁâ©ÂìÅ' }}
        </el-button>
        <el-button @click="detailDialogVisible = false">ÂÖ≥Èó≠</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios';
import dayjs from 'dayjs';

export default {
  /* eslint-disable no-undef */
  // Âú®dataÈÉ®ÂàÜ‰øÆÊîπ
  data() {
    return {
      // Êñ∞Â¢ûËøáÊª§Áõ∏ÂÖ≥Êï∞ÊçÆ
      searchQuery: '',
      locationQuery: '',  // Êñ∞Â¢ûÂú∞ÂùÄÊêúÁ¥¢
      filterType: null,
      typeOptions: [], // ÁßªÈô§Á°¨ÁºñÁ†ÅÈÄâÈ°π
      categories: [],  // Êñ∞Â¢ûÂàÜÁ±ªÊï∞ÊçÆ
      lostItems: [],
      loading: false,
      detailDialogVisible: false,
      currentItem: {},
      statusTypeMap: {
        pending: 'warning',
        active: 'primary',
        completed: 'success',
        expired: 'info'
      },
      statusTextMap: {
        pending: 'ÂæÖÂÆ°Ê†∏',
        active: 'ËøõË°å‰∏≠',
        completed: 'Â∑≤ÂÆåÊàê',
        expired: 'Â∑≤ËøáÊúü'
      },
      bookmarked: false,
    };
  },
  methods: {
    formatTime(time) {
      return dayjs(time).format('YYYY-MM-DD HH:mm:ss')
    },
    // Êñ∞Â¢ûËøáÊª§Â§ÑÁêÜÊñπÊ≥ï
    handleFilter() {
      // Â¶ÇÊûúÈúÄË¶ÅÂêéÁ´ØËøáÊª§ÔºåÂèØ‰ª•Ë∞ÉÁî®APIÔºö
      this.fetchLostItems(this.filterParams)
    },
    async fetchCategories() {
      try {
        const response = await axios.get('/api/lost/categories/tree/');
        this.categories = this.convertToFlatOptions(response.data);
      } catch (error) {
        console.error('Ëé∑ÂèñÂàÜÁ±ªÂ§±Ë¥•', error);
      }
    },
    convertToFlatOptions(tree) {
      const flatten = (items) => {
        return items.reduce((acc, item) => {
          acc.push({
            value: item.id,
            label: item.name,
            hasChildren: item.children && item.children.length > 0
          });
          if (item.children) {
            acc.push(...flatten(item.children));
          }
          return acc;
        }, []);
      };
      return flatten(tree);
    },
    // ‰øÆÊîπÂéüÊúâÁöÑËé∑ÂèñÊï∞ÊçÆÊñπÊ≥ï‰ª•ÊîØÊåÅÂèÇÊï∞
    async fetchLostItems() {
      this.loading = true
      try {
        const response = await axios.get('/api/public/lost-items/', {
          headers: {
            'Authorization': `Token ${this.$store.state.token}`,
            'X-CSRFToken': this.getCSRFToken()
          }
        })
        this.lostItems = response.data
      } catch (error) {
        console.error('Ëé∑ÂèñÂ§±Áâ©‰ø°ÊÅØÂ§±Ë¥•', error)
      } finally {
        this.loading = false
      }
    },
    async checkBookmarkStatus() {
      try {
        // ‰øÆÊîπËØ∑Ê±ÇË∑ØÂæÑ‰∏∫ÂêéÁ´ØÂÆûÈôÖÊé•Âè£Ë∑ØÂæÑ
        const response = await axios.get(`/api/bookmarks/${this.currentItem.id}/`, {
          headers: {
            'Authorization': `Token ${this.$store.state.token}`,
            'X-CSRFToken': this.getCSRFToken(),
          }
        });
        this.bookmarked = response.data.bookmarked;
      } catch (error) {
        console.error('Ëé∑ÂèñÊî∂ËóèÁä∂ÊÄÅÂ§±Ë¥•', error);
      }
    },
    getCSRFToken() {
      const cookieValue = document.cookie
        .split('; ')
        .find(row => row.startsWith('csrftoken='))
        ?.split('=')[1] || '';
      return cookieValue;
    },
    async handleBookmark() {
      try {
        const config = {
          headers: {
            'Authorization': `Token ${this.$store.state.token}`,
            'X-CSRFToken': this.getCSRFToken(),
            'Content-Type': 'application/json'
          }
        };

        // ‰øÆÊ≠£ÂèÇÊï∞È°∫Â∫èÔºàDELETEÊñπÊ≥ïÁ¨¨‰∫å‰∏™ÂèÇÊï∞ÊòØconfigÔºâ
        const method = this.bookmarked ? 'delete' : 'post';
        await axios[method](
          `/api/bookmarks/${this.currentItem.id}/`,
          method === 'delete' ? config : null, // ‰ªÖDELETEÈúÄË¶ÅconfigÂú®Á¨¨‰∫å‰∏™ÂèÇÊï∞
          method === 'post' ? config : null         // POSTÈúÄË¶ÅÁ©∫Êï∞ÊçÆ‰Ωì
        );

        this.bookmarked = !this.bookmarked;
        this.$message.success(this.bookmarked ? 'Êî∂ËóèÊàêÂäü' : 'Â∑≤ÂèñÊ∂àÊî∂Ëóè');
      } catch (error) {
        console.error('Êìç‰ΩúÊî∂ËóèÂ§±Ë¥•', error);
        this.$message.error(`Êìç‰ΩúÂ§±Ë¥•: ${error.response?.data?.error || 'Êú™Áü•ÈîôËØØ'}`);
      }
    },
    viewDetails(item) {
      if (!this.$store.state.token) {
        this.$message.warning('ËØ∑ÂÖàÁôªÂΩï');
        return;
      }
      this.currentItem = item;
      this.detailDialogVisible = true;
      this.checkBookmarkStatus();
    },
    // ÂàùÂßãÂåñËØ¶ÊÉÖÂú∞Âõæ
    initDetailMap() {
      if (!window.AMap) {
        this.$message.warning('Âú∞ÂõæËµÑÊ∫êÊ≠£Âú®Âä†ËΩΩÔºåËØ∑Á®çÂÄô')
        return
      }
      const lng = parseFloat(this.currentItem.location_lng)
      const lat = parseFloat(this.currentItem.location_lat)
      if (isNaN(lng) || isNaN(lat)) return

      this.destroyDetailMap()

      const mapContainerId = `detail-map-${this.currentItem.id}`
      const mapContainer = document.getElementById(mapContainerId)
      if (!mapContainer) return

      this.detailMap = new AMap.Map(mapContainerId, {
        zoom: 17,
        center: [lng, lat],
        resizeEnable: true
      })

      // ÂÆû‰æãÂåñÁã¨Á´ãÊéß‰ª∂
      const scale = new AMap.Scale()
      const toolBar = new AMap.ToolBar({
        position: { bottom: '20px', right: '20px' }
      })

      // ÈÄê‰∏™Ê∑ªÂä†Êéß‰ª∂
      scale.addTo(this.detailMap)
      toolBar.addTo(this.detailMap)

      // Ê∑ªÂä†Ê†áËÆ∞
      new AMap.Marker({
        position: [lng, lat],
        content: '<div class="location-pin">üìç</div>',
        map: this.detailMap
      })
    },
    destroyDetailMap() {
      if (this.detailMap) {
        try {
          this.detailMap.destroy()
        } catch (e) {
          console.warn('Âú∞ÂõæÈîÄÊØÅËøáÁ®ã‰∏≠Âá∫Áé∞Ë≠¶Âëä:', e.message)
        }
        this.detailMap = null
      }
    },
    getItemTypeLabel(itemType) {
      return {
        'lost': 'Â§±Áâ©ÁôªËÆ∞',
        'found': 'ÊãõÈ¢ÜÁôªËÆ∞'
      }[itemType] || 'Êú™Áü•Á±ªÂûã';
    },
    async sendNotification(itemId) {
      try {
        await axios.post(`/api/report-found/${itemId}/`, {}, {
          headers: {
                'Authorization': `Token ${this.$store.state.token}`,
                'X-CSRFToken': this.getCSRFToken(),
            }
        });
        this.$message.success('ÈÄöÁü•Â∑≤ÂèëÈÄÅ');
      } catch (error) {
        console.error('ÂèëÈÄÅÈÄöÁü•Â§±Ë¥•', error);
        this.$message.error('Êìç‰ΩúÂ§±Ë¥•ÔºåËØ∑ÈáçËØï');
      }
    },
  },
  mounted() {
    this.fetchCategories();
    this.fetchLostItems();
    if (!window.AMap) {
      const key = 'db70318a1cf1f196b2746f10cb9df826'
      const plugins = [
        'AMap.Scale',
        'AMap.ToolBar'
      ].join(',')
      const script = document.createElement('script')
      script.src = `https://webapi.amap.com/maps?v=2.0&key=${key}&plugin=${plugins}`
      script.onerror = () => {
        console.error('È´òÂæ∑Âú∞ÂõæSDKÂä†ËΩΩÂ§±Ë¥•')
      }
      document.head.appendChild(script)
    }
  },
  computed: {
    roleMap() {
      return {
        admin: 'ÁÆ°ÁêÜÂëò',
        teacher: 'ÊïôËÅåÂ∑•',
        student: 'Â≠¶Áîü'
      }
    },
    // Ê∑ªÂä†ËøáÊª§ËÆ°ÁÆóÂ±ûÊÄß
    filteredItems() {
      const query = this.searchQuery.toLowerCase()
      const locationQuery = this.locationQuery.toLowerCase()
      return this.lostItems.filter(item =>
        (item.title.toLowerCase().includes(query) ||
          item.description.toLowerCase().includes(query)) &&
        (item.location.toLowerCase().includes(locationQuery)) &&
        (!this.filterType || item.category === this.filterType)
      )
    }

  },
  watch: {
    detailDialogVisible(newVal) {
      if (newVal) {
        this.$nextTick(() => {
          if (this.currentItem.location_lat && this.currentItem.location_lng) {
            this.initDetailMap();
          }
        });
      } else {
        this.destroyDetailMap();
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.lost-item-hall {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 24px;
  background: white;
  border-radius: 12px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.08);

  h1 {
    text-align: center;
    color: #303133;
    font-size: 28px;
    margin-bottom: 40px;
    position: relative;

    &::after {
      content: '';
      display: block;
      width: 60px;
      height: 3px;
      background: #409EFF;
      margin: 12px auto 0;
      border-radius: 2px;
    }
  }
}

.empty-state {
  text-align: center;
  padding: 20px;
  color: #606266;
}

.text-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.detail-image {
  width: 150px;
  height: 150px;
  margin-right: 12px;
  border: 1px solid #ebeef5;
  border-radius: 8px;
  transition: transform 0.3s;

  &:hover {
    transform: scale(1.05);
    cursor: zoom-in;
  }
}

.description-pre {
  white-space: pre-wrap;
  line-height: 1.6;
  margin: 0;
  font-family: inherit;
}

// Ê∑ªÂä†ËØ¶ÊÉÖÂú∞ÂõæÊ†∑Âºè
.detail-map-container {
  width: 100%;
  height: 200px;
  margin-top: 12px;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  position: relative;

  &::after {
    content: 'È´òÂæ∑Âú∞ÂõæÊèê‰æõÊîØÊåÅ';
    position: absolute;
    right: 5px;
    bottom: 5px;
    font-size: 10px;
    color: #666;
    background: rgba(255, 255, 255, 0.8);
    padding: 2px 5px;
    border-radius: 3px;
  }
}

// Ê†áËÆ∞ÁÇπÊ†∑Âºè
.detail-marker {
  font-size: 24px;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
  transform: translate(-12px, -24px);
}

.toolbar {
  margin-bottom: 20px;

  .search-item {
    padding: 8px;
  }
}

.search-input {
  ::v-deep .el-input__prefix {
    left: 8px; // Ë∞ÉÊï¥ÊêúÁ¥¢ÂõæÊ†áÂ∑¶Èó¥Ë∑ù
    display: flex;
    align-items: center;
  }
}

.type-selector {
  width: 100%;

  ::v-deep .el-input__prefix {
    left: 8px;
    pointer-events: none; // Á¶ÅÊ≠¢ÁÇπÂáªÂõæÊ†áËß¶ÂèëËæìÂÖ•
  }

  .category-option {
    i {
      margin-left: 8px;
      font-size: 14px; // Ë∞ÉÊï¥Êñá‰ª∂Â§πÂõæÊ†áÂ§ßÂ∞è
      vertical-align: middle;
    }
  }
}

.location-input {
  ::v-deep .el-input__prefix {
    left: 8px;
    display: flex;
    align-items: center;
  }
}

.input-icon {
  margin-left: 0; // ÁßªÈô§ÂéüÊúâÂ∑¶ËæπË∑ù
  color: #409EFF;
  font-size: 16px; // Áªü‰∏ÄÂõæÊ†áÂ∞∫ÂØ∏
}
</style>